import { nonSeeded } from "../fable_modules/fable-library.3.7.9/Random.js";
import { ofArray, length, item } from "../fable_modules/fable-library.3.7.9/List.js";
import { GameObject, Enemy, EnemyStateType, Vector2D__Abs, Vector2D, Vector2D__IsBetween, Vector2D__Normalize, Vector2D__Rotate_Z114CB6F } from "../Model.fs.js";
import { cast } from "./Ray.fs.js";
import { equals } from "../fable_modules/fable-library.3.7.9/Util.js";

const List_randomGenerator = nonSeeded();

export function List_random(values) {
    return item(List_randomGenerator.Next2(0, length(values)), values);
}

export function inFieldOfView(game, enemy) {
    const matchValue = enemy.DirectionVector;
    if (matchValue != null) {
        const directionVector = matchValue;
        const fieldOfViewAngle = ((45 * 3.141592653589793) / 180) * 1;
        const boundingVectorA = Vector2D__Rotate_Z114CB6F(Vector2D__Normalize(directionVector), -fieldOfViewAngle);
        const boundingVectorB = Vector2D__Rotate_Z114CB6F(Vector2D__Normalize(directionVector), fieldOfViewAngle);
        return Vector2D__IsBetween(Vector2D__Normalize(new Vector2D(game.Camera.Position.vX - enemy.BasicGameObject.Position.vX, game.Camera.Position.vY - enemy.BasicGameObject.Position.vY)), boundingVectorA, boundingVectorB);
    }
    else {
        return true;
    }
}

export function isPlayerVisibleToEnemy(game, enemy) {
    if (inFieldOfView(game, enemy)) {
        const vectorToEnemy = new Vector2D(game.Camera.Position.vX - enemy.BasicGameObject.Position.vX, game.Camera.Position.vY - enemy.BasicGameObject.Position.vY);
        const absVectorToEnemy = Vector2D__Abs(vectorToEnemy);
        const rayDirection = Vector2D__Normalize(vectorToEnemy);
        return !cast(() => [enemy.BasicGameObject.Position.vX, enemy.BasicGameObject.Position.vY, rayDirection], (tupledArg) => {
            const mapX = tupledArg[3] | 0;
            const mapY = tupledArg[4] | 0;
            if ((!tupledArg[0]) && ((((mapX >= 0) && (mapX < length(item(0, game.Map)))) && (mapY >= 0)) && (mapY < length(game.Map)))) {
                if (Math.abs(tupledArg[1]) < absVectorToEnemy.vX) {
                    return true;
                }
                else {
                    return Math.abs(tupledArg[2]) < absVectorToEnemy.vY;
                }
            }
            else {
                return false;
            }
        }, game)[0];
    }
    else {
        return false;
    }
}

export function getNextState(canSeePlayer, enemy) {
    const matchValue = [enemy.State, canSeePlayer];
    let pattern_matching_result;
    if (matchValue[0].tag === 0) {
        if (matchValue[1]) {
            pattern_matching_result = 0;
        }
        else {
            pattern_matching_result = 1;
        }
    }
    else if (matchValue[0].tag === 1) {
        if (matchValue[1]) {
            pattern_matching_result = 0;
        }
        else {
            pattern_matching_result = 1;
        }
    }
    else {
        pattern_matching_result = 1;
    }
    switch (pattern_matching_result) {
        case 0: {
            return List_random(ofArray([() => (new EnemyStateType(2)), () => (new EnemyStateType(6))]))();
        }
        case 1: {
            return enemy.State;
        }
    }
}

export function applyAi(game, gameObject) {
    if (gameObject.tag === 1) {
        const enemy = gameObject.fields[0];
        const newState = getNextState(isPlayerVisibleToEnemy(game, enemy), enemy);
        return new GameObject(1, (!equals(newState, enemy.State)) ? (new Enemy(enemy.EnemyType, enemy.BasicGameObject, enemy.DirectionVector, enemy.Direction, enemy.DeathSpriteIndexes, enemy.AttackSpriteIndexes, enemy.SpriteBlocks, enemy.FramesPerBlock, enemy.CurrentAnimationFrame, enemy.TimeUntilNextAnimationFrame, newState)) : enemy);
    }
    else {
        return gameObject;
    }
}

