
export const isShareware = true;

export const extension = isShareware ? "WL1" : "WL6";

export const GAMEMAPS = `GAMEMAPS.${extension}`;

export const MAPHEAD = `MAPHEAD.${extension}`;

export const VSWAP = `VSWAP.${extension}`;

export function isSharewareSprite(index) {
    if ((index <= 186) ? true : ((index >= 296) && (index <= 306))) {
        return true;
    }
    else if (index >= 408) {
        return index <= 435;
    }
    else {
        return false;
    }
}

export function audioFilename(extension_1, soundEffect) {
    const prefix = (soundEffect.tag === 1) ? "Aarrrgh (enemy death)" : ((soundEffect.tag === 2) ? "Aieeee1 (enemy death)" : ((soundEffect.tag === 3) ? "Aieeee2 (enemy death)" : ((soundEffect.tag === 4) ? "DoorOpen" : ((soundEffect.tag === 5) ? "DoorClose" : "Pistol"))));
    return `${prefix}.${extension_1}`;
}

